"use strict";
import React,{Component, PropTypes} from 'react'
import './PaySuccess.scss'
import IsPcOrNot from '../../../components/lib/IsPcOrNot.js'
import en from '../../../locale/en';
import zh from '../../../locale/zh';
import Header from '../../../components/Header/Header'
import Footer from '../../../components/Footer/Footer'
import $ from 'n-zepto'
import Constant from '../../../components/lib/Constant.js'
import {FormattedMessage} from '../../../components/lib/BaseComponent'
let messages = {};
messages['en'] = en;
messages['zh-CN'] = zh;
messages['zh-cn'] = zh;

class Phone extends Component {
    constructor(props) {
        super(props);
    }
    componentDidMount() {

    }
    render() {
        return <div style={{backgroundColor:"#fff"}}>
            <Header/>
            <div style={{margin:"20px auto",textAlign:"center",minHeight:"680px",fontSize:"20px"}}>
                <FormattedMessage id="pay_success"/>
            </div>
            <Footer/>
        </div>
    }
}
class PC extends Component {
    constructor(props) {
        super(props);
    }
    componentDidMount() {

    }
    render() {
        return <div style={{backgroundColor:"#fff"}}>
            <Header/>
            <div style={{margin:"200px auto",width:"500px",lineHeight:"40px",fontWeight:"600",textAlign:"center",minHeight:"680px",fontSize:"24px"}}>
                <FormattedMessage id="pay_success"/>
            </div>
            <Footer/>
        </div>
    }
}
export class PaySuccess extends Component {
    constructor(props) {
        super(props);
    }
    componentDidMount() {
        setTimeout(()=>{
            location.href = Constant.router_preffix + '/my_tickets';
        },5000);
    }
    render() {
        return <div>
                {
                    IsPcOrNot.get() ?
                        <PC/> : <Phone/>
                }
            </div>
    }
}
export default PaySuccess;


